import * as React from "react";
import BootstrapNavbar from "./Navbar/navbar";
import { Container } from "react-layout-components";
import { Link, useStaticQuery, graphql } from "gatsby";
import { pageContainer, container, heading, } from "./layout.module.css";
import Footer from "./footer";
import Button from "react-bootstrap/Button"
import favicon from "../images/favicon.ico"

const pathPrefix = "shpe_gpc_react/";
const Layout = ({ pageTitle, children }) => {
  const data = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          title
        }
      }
    }
  `);

  return (
    <Container className={pageContainer}>
      
      <div style={{ marginBottom: "50px" }}>
        <BootstrapNavbar />
      </div>
      <div >
        <title>
          {pageTitle} | {data.site.siteMetadata.title}
        </title>
        <main className={container}>
          
          <div></div>

          {children}
        </main>

        <footer id="footer">

        
        <a href="https://forms.gle/sRWWVXZZgMMjayPZ9" target="_blank" rel="noopener noreferrer">
        <Button>
          Click Here to Subscribe To Our News Letter!
        </Button>
          </a>
        
      </footer>
        
      </div>
      
      

    </Container>
  );
};

export default Layout;
